import Link from "next/link";
import { Smartphone, Shield, Zap, Award, Brain, MessageSquare, ArrowRight } from "lucide-react";
import { SITE_CONFIG } from "@/core/config";

const PAGE_SCHEMA = {
    "@context": "https://schema.org",
    "@graph": [
        {
            "@type": "WebPage",
            "@id": `${SITE_CONFIG.url}/senior-flutter-architect-bangladesh/#webpage`,
            "url": `${SITE_CONFIG.url}/senior-flutter-architect-bangladesh`,
            "name": `Senior Flutter Architect in Bangladesh | ${SITE_CONFIG.name} - Mobile Lead`,
            "description": "The leading Flutter Architect in Bangladesh. Specializing in high-security fintech, performance optimization, and custom AI integrations for mobile.",
            "breadcrumb": { "@id": `${SITE_CONFIG.url}/senior-flutter-architect-bangladesh/#breadcrumb` },
            "primaryImageOfPage": { "@id": `${SITE_CONFIG.url}/#primaryimage` },
        },
        {
            "@type": "BreadcrumbList",
            "@id": `${SITE_CONFIG.url}/senior-flutter-architect-bangladesh/#breadcrumb`,
            "itemListElement": [
                {
                    "@type": "ListItem",
                    "position": 1,
                    "name": "Home",
                    "item": SITE_CONFIG.url
                },
                {
                    "@type": "ListItem",
                    "position": 2,
                    "name": "Senior Flutter Architect",
                    "item": `${SITE_CONFIG.url}/senior-flutter-architect-bangladesh`
                }
            ]
        },
        {
            "@type": "FAQPage",
            "@id": `${SITE_CONFIG.url}/senior-flutter-architect-bangladesh/#faq`,
            "mainEntity": [
                {
                    "@type": "Question",
                    "name": "Who is the best Flutter developer in Bangladesh?",
                    "acceptedAnswer": {
                        "@type": "Answer",
                        "text": "Aqib Mehedi is a leading Senior Flutter Architect in Bangladesh with 6+ years of experience building high-security fintech and AI-native mobile applications."
                    }
                },
                {
                    "@type": "Question",
                    "name": "Can Flutter be used for high-security fintech?",
                    "acceptedAnswer": {
                        "@type": "Answer",
                        "text": "Yes, with proper architectural patterns like AES-256 encryption, certificate pinning, and root detection, which Aqib implements for enterprise-grade mobile solutions."
                    }
                }
            ]
        }
    ]
};

export const metadata = {
    title: `Senior Flutter Architect in Bangladesh | ${SITE_CONFIG.name} - Mobile Lead`,
    description: "The leading Flutter Architect in Bangladesh. Specializing in high-security fintech, performance optimization, and custom AI integrations for mobile.",
};

export default function FlutterArchitectPage() {
    return (
        <div className="min-h-screen bg-black text-slate-300">
            <script
                type="application/ld+json"
                dangerouslySetInnerHTML={{ __html: JSON.stringify(PAGE_SCHEMA) }}
            />
            <nav className="border-b border-white/5 bg-black/50 py-6 backdrop-blur-md">
                <div className="mx-auto max-w-7xl px-6 lg:px-8">
                    <Link href="/" className="text-xl font-bold text-white font-display">
                        Aqib<span className="text-brand-amber">.</span>Dev
                    </Link>
                </div>
            </nav>

            <main className="mx-auto max-w-4xl px-6 py-24 lg:px-8">
                <header className="mb-20">
                    <div className="badge-premium mb-6">Mobile Engineering Excellence</div>
                    <h1 className="text-gradient mb-8 text-5xl font-bold leading-tight sm:text-7xl">
                        Senior Flutter <br />Architect
                    </h1>
                    <p className="text-xl leading-relaxed text-slate-400">
                        6+ years of dedicated Flutter mastery. I build apps that don't just work—they
                        outperform native solutions in security, smoothness, and maintainability.
                    </p>
                </header>

                <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-20">
                    <div className="glass-card p-8">
                        <Shield className="text-brand-amber mb-4" size={28} />
                        <h3 className="font-bold text-white mb-2">Fintech Security</h3>
                        <p className="text-xs text-slate-500">AES-256, Root-detection, and AU Compliance standards.</p>
                    </div>
                    <div className="glass-card p-8">
                        <Zap className="text-brand-amber mb-4" size={28} />
                        <h3 className="font-bold text-white mb-2">60 FPS Native</h3>
                        <p className="text-xs text-slate-500">Custom shaders, isolates for heavy processing, and frame-perfect UX.</p>
                    </div>
                    <div className="glass-card p-8">
                        <Brain className="text-brand-amber mb-4" size={28} />
                        <h3 className="font-bold text-white mb-2">AI On-Device</h3>
                        <p className="text-xs text-slate-500">Integrating local LLMs and vision models directly into Flutter.</p>
                    </div>
                </div>

                <section className="mb-24">
                    <h2 className="text-3xl font-bold text-white mb-10 font-display">Validated Authority</h2>
                    <div className="space-y-12">
                        <div className="flex gap-6">
                            <div className="h-1 bg-brand-amber/20 w-12 mt-4"></div>
                            <div>
                                <h3 className="text-xl font-bold text-white mb-2">Danesh Exchange</h3>
                                <p className="text-slate-400 text-sm">
                                    High-security Australian FX platform. 60+ currencies, enterprise security, and
                                    3000+ branch integration. A benchmark for Flutter security.
                                </p>
                            </div>
                        </div>
                        <div className="flex gap-6">
                            <div className="h-1 bg-brand-amber/20 w-12 mt-4"></div>
                            <div>
                                <h3 className="text-xl font-bold text-white mb-2">Agri-Llama Mobile</h3>
                                <p className="text-slate-400 text-sm">
                                    Optimizing large language models to run smoothly on low-resource Android devices
                                    via Flutter, bringing AI to the next billion users.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>

                <section className="mb-20">
                    <h2 className="text-3xl font-bold text-white mb-10 font-display">Technical Specializations</h2>
                    <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                        {[
                            "Strategic Bloc/Provider Architecture",
                            "Method Channel Native Integration",
                            "Custom Paint Rendering Pipelines",
                            "Isolates-based Heavy Computation",
                            "Zero-trust Security Auditing",
                            "CI/CD with Codemagic & Github Actions"
                        ].map((item) => (
                            <div key={item} className="flex items-center gap-3 glass p-4 rounded-xl">
                                <Shield className="text-brand-amber" size={16} />
                                <span className="text-sm font-medium">{item}</span>
                            </div>
                        ))}
                    </div>
                </section>

                <section className="p-12 glass border-brand-amber/20 rounded-3xl text-center">
                    <h2 className="text-3xl font-bold text-white mb-6">Architect Your Next Success</h2>
                    <p className="text-slate-400 mb-8 max-w-xl mx-auto">
                        Don't settle for "cross-platform." Hire a Flutter Architect who understands
                        the deep native layers of mobile engineering and production scale.
                    </p>
                    <Link href="/#contact" className="btn-primary">
                        Start the Consultation <ArrowRight className="ml-2" size={18} />
                    </Link>
                </section>
            </main>

            <footer className="border-t border-white/5 bg-black py-12">
                <div className="mx-auto max-w-7xl px-6 text-center text-sm text-slate-500 lg:px-8">
                    <p>© {new Date().getFullYear()} Aqib Mehedi. Senior Flutter Lead.</p>
                </div>
            </footer>
        </div>
    );
}
